import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useForm } from "react-hook-form";
import firebase from "gatsby-plugin-firebase"

import { selectUserData, createDataItem, editDataItem } from "../features/data/dataSlice";
import { selectApplication } from "../features/application/applicationSlice";

import { dictionary_ru_financial_reports } from "../laboratory/dictionary_ru_financial_reports";

import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";

function doFindValuesForForm(data, formMetaData, currentProjectTitle) {
  //  console.log(data);
  //  console.log(formMetaData);
    let foundInitialValues = {};
    formMetaData.reportIndicators.map(indicator => {
        formMetaData.reportDates.map(period => {
            let item = indicator + '___' + period;
            let found = data.find(dataItem => dataItem.code === indicator && dataItem.period === period && dataItem.currentProjectTitle === currentProjectTitle) ; //   dataItem.form_name === item
            if (!!found) { foundInitialValues[item] = found.value }
        })
    });
    return foundInitialValues
}

export default function EditableFinancialTable(props) {
    const userData = useSelector(selectUserData);
    const applicationState = useSelector(selectApplication);
    const dispatch = useDispatch();

    const { register, handleSubmit, watch, errors } = useForm({ defaultValues: doFindValuesForForm(userData.data, props.formMetaData, applicationState.currentProjectTitle) });

   // console.log(doFindValuesForForm(userData.data, props.formMetaData));

    function onSubmit(data) {

        let date = new Intl.DateTimeFormat("ru", {
            weekday: "short",
            year: "numeric",
            month: "short",
            day: "numeric",
            hour: "numeric",
            minute: "numeric",
        }).format(new Date());

        props.formMetaData.reportIndicators.map(indicator => {
            props.formMetaData.reportDates.map(period => {
                let item = indicator + '___' + period;
                let found = userData.data.find(dataItem => dataItem.form_name === item);
                if (!found) {
                    let userEmail = applicationState.email.replace(/[^a-zA-Z0-9]/g, "_");
                    let idUserDataItem = firebase.database().ref(userEmail).child('data').push().key;
                    dispatch(createDataItem({
                        id: idUserDataItem,
                        currentProjectTitle: applicationState.currentProjectTitle,
                        currentProjectComment: applicationState.currentProjectComment,
                        currentProjectOpenMediaDataURL: applicationState.currentProjectOpenMediaDataURL,
                        currentProjectSourseDataURL: applicationState.currentProjectSourseDataURL,
                        currentProjectMoneyScale: applicationState.currentProjectMoneyScale,
                        form_name: item,
                        code: indicator,
                        ru_title: dictionary_ru_financial_reports[indicator].ru_title,
                        ru_comment: dictionary_ru_financial_reports[indicator].ru_comment,
                        ru_source: dictionary_ru_financial_reports[indicator].ru_source,
                        period: period,
                        value: data[item],
                        date: date
                    }))
                }
                else {
                    if (found.value !== data[item]) {
    //                   console.log('Надо изменить значение ' + item);
                        dispatch(editDataItem({ ...found, value: data[item], date: date }));
                    }
                }

            })
        })
  //      console.log(data)
    }

    return <form onSubmit={handleSubmit(onSubmit)}>

        <InputGroup className="mb-3" size="sm">
            <InputGroup.Prepend><InputGroup.Text style={{ width: '15rem' }}>Показатели</InputGroup.Text></InputGroup.Prepend>
            {props.formMetaData.reportDates.map(item =>
                <FormControl
                    key={item}
                    placeholder={item}
                    readOnly
                />
            )}
        </InputGroup>

        {props.formMetaData.reportIndicators.map(row =>
            <InputGroup className="mb-3" size="sm" key={row}>
                <InputGroup.Prepend><InputGroup.Text style={{ width: '15rem' }}>{dictionary_ru_financial_reports[row].ru_title}</InputGroup.Text></InputGroup.Prepend>
                {props.formMetaData.reportDates.map(period =>
                    <FormControl
                        key={row + '___' + period}
                        name={row + '___' + period}
                        type="number"
                        step="0.1"
                        ref={register}
                        required
                    />
                )}
            </InputGroup>
        )}



        {/* errors will return when field validation fails {errors.sales_2019 && <span>This field is required</span>} */}

        {applicationState?.currentProjectOpenMediaDataURL && applicationState?.currentProjectOpenMediaDataURL.length > 6 ?
            <Button variant="outline-secondary" type="submit" size="sm">Сохранить</Button> : null}

    </form>
}