import React from "react";

const ApexBarChartsGroupedLazy = React.lazy(() => import("./lazy/ApexBarChartsGroupedLazy.js"))

export default (props) => {
    const isSSR = typeof window === "undefined"
    return <>
      {!isSSR && (
        <React.Suspense fallback={<div />}><ApexBarChartsGroupedLazy {...props} /></React.Suspense>
      )}
    </>
}
  
  
