let modalForm = [
    { name: "currentProjectTitle", title: "Название проекта" },
    { name: "currentProjectComment", title: "Комментарий" },
    // "currentProjectOpenMediaDataURL",
    { name: "currentProjectSourseDataURL", title: "Интернет источник" },
    { name: "currentProjectMoneyScale", title: "Денежная единица (млн. руб, млрд долл и т.п.)" }
];

function NewProjectForm() {
    const { register, handleSubmit, watch, errors } = useForm();
    const dispatch = useDispatch();

    function onSubmit(data) {
        console.log(data);
        let newProject = {
            ...data,
            currentProjectOpenMediaDataURL: firebase.database().ref('openmediadata').push().key
        }
        dispatch(set_currentProject(newProject))
    }

    return <form onSubmit={handleSubmit(onSubmit)}>

        {modalForm.map(item =>
            <InputGroup className="mb-3" size="sm" key={item.name}>
                <InputGroup.Prepend><InputGroup.Text style={{ width: '15rem' }}>{item.title}</InputGroup.Text></InputGroup.Prepend>
                <FormControl
                    key={item.name}
                    name={item.name}
                    type="text"
                    ref={register}
                />
            </InputGroup>
        )}
        <Button variant="outline-secondary" type="submit" size="sm">Сохранить</Button>
    </form>
}