import React from "react";
import { useSelector, useDispatch } from "react-redux";

import { selectUserData, createDataItem, editDataItem } from "../features/data/dataSlice";
import { selectApplication } from "../features/application/applicationSlice";

import { dictionary_ru_financial_reports } from "./dictionary_ru_financial_reports";


import ApexBarChartsGrouped from "../mediatemplates/ApexBarChartsGrouped.js";

import Container from "react-bootstrap/Container";

function doFindValuesForChart(data, formMetaData, currentProjectTitle) {
    let barsData = [];
    formMetaData.reportIndicators.map(indicator => {
        let barData = []
        formMetaData.reportDates.map(period => {
            let found = data.find(dataItem => dataItem?.code === indicator && dataItem?.period === period && dataItem?.currentProjectTitle === currentProjectTitle); //   dataItem.form_name === item
            if (!!found) { barData.push(found.value) } else { barData.push(null)}
        })
        barsData.push({ name: dictionary_ru_financial_reports[indicator].ru_title, data: barData })
    });
  //  console.log(barsData);
    return barsData
  
}

export default function BarsGroupedByPeriod(props) {
    const userData = useSelector(selectUserData);
    const applicationState = useSelector(selectApplication);

//    console.log(props);

    let series = doFindValuesForChart(userData.data, props.formMetaData, applicationState.currentProjectTitle);

    let categories = !!props.formMetaData?.reportDates ? props.formMetaData.reportDates : [2001, 2002];

    return <Container className="mt-3" >
        <ApexBarChartsGrouped
            options={!!props?.options ? {...props.options}: {}}
            series={series}
            categories={categories}
        />
    </Container>
}


